___CSS_LOADER_EXPORT___.push([module.id, "\n.login-box .van-tabs__nav--line{\n  border-top: 2px #263447 solid;\n  background-color: transparent\n}\n.login-box .van-tabs__line{\n  top: -2px;\n  bottom: unset;\n  background-color: #06F0FE;\n  width: 60px;\n}\n.login-box .van-tab{\n  font-size: 16px\n}\n.login-box .van-cell{\n  background-color: transparent;\n  padding-left: 7px;\n  font-size: 16px\n}\n.login-box .van-tabs--line .van-tabs__wrap{\n  width: 54%;\n  margin: 0 auto;\n}\n.login-box .van-tabs__content{\n  width: 80%;\n  margin: 0 auto\n}\n.login-box .form-box input::-webkit-input-placeholder {\n  color: #373F4A;\n}\n.login-box .form-box input::-moz-input-placeholder {\n  color: #373F4A;\n}\n.login-box .form-box input::-ms-input-placeholder {\n  color: #373F4A;\n}\n.login-box .form-box input:focus {\n  color: rgba(139, 163, 194, 1) !important\n}\n/* input失去焦点时文字颜色 */\n.login-box .van-field__control{\n  color: #8BA3C2\n}\n", "",{"version":3,"sources":["webpack://./src/views/login/login.vue"],"names":[],"mappings":";AAwOA;EACA,6BAAA;EACA;AACA;AACA;EACA,SAAA;EACA,aAAA;EACA,yBAAA;EACA,WAAA;AACA;AACA;EACA;AACA;AACA;EACA,6BAAA;EACA,iBAAA;EACA;AACA;AACA;EACA,UAAA;EACA,cAAA;AACA;AACA;EACA,UAAA;EACA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA;AACA;AACA,mBAAA;AACA;EACA;AACA","sourcesContent":["<template>\n  <div class=\"login-box\">\n    <div class=\"logo-box\">\n      <Adaptive :data=\"['100%', '32%']\">\n        <van-image\n          width=\"100%\"\n          height=\"100%\"\n          fit=\"cover\"\n          :src=\"require('/src/assets/images/login/logo.png')\"\n          :show-error=\"false\"\n          :show-loading=\"false\"\n        />\n      </Adaptive>\n    </div>\n    <p class=\"title\">\n      高新建投智慧视觉平台\n    </p>\n    <div class=\"tab-box\">\n      <van-tabs v-model=\"activeTab\" title-active-color=\"#06F0FE\" title-inactive-color=\"#6F85A2\" line-height=\"2px\">\n        <van-tab title=\"短信登录\">\n          <div class=\"form-box\">\n            <div class=\"form-content\">\n              <!-- 输入手机号，调起手机号键盘 -->\n              <van-image\n                width=\"40px\"\n                height=\"44px\"\n                fit=\"cover\"\n                :src=\"require('/src/assets/images/login/phone.png')\"\n                :show-error=\"false\"\n                :show-loading=\"false\"\n              />\n              <van-field v-model=\"isSMSData.phone\" type=\"tel\" placeholder=\"请输入手机号\" />\n            </div>\n            <div class=\"form-content\">\n              <van-image\n                width=\"40px\"\n                height=\"44px\"\n                fit=\"cover\"\n                :src=\"require('/src/assets/images/login/code.png')\"\n                :show-error=\"false\"\n                :show-loading=\"false\"\n              />\n              <van-field\n                v-model=\"isSMSData.code\"\n                center\n                clearable\n                placeholder=\"请输入验证码\"\n              >\n                <template #button>\n                  <van-button class=\"send-code\" size=\"small\" type=\"primary\" @click=\"getPhoneCode()\">\n                    {{ yzm_state ? yzm_state + \"秒\" : \"发送验证码\" }}\n                  </van-button>\n                </template>\n              </van-field>\n            </div>\n          </div>\n        </van-tab>\n        <van-tab title=\"账号登录\">\n          <div class=\"form-box\">\n            <div class=\"form-content\">\n              <!-- 输入用户名称，调起手机号键盘 -->\n              <van-image\n                width=\"40px\"\n                height=\"44px\"\n                fit=\"cover\"\n                :src=\"require('/src/assets/images/login/userName.png')\"\n                :show-error=\"false\"\n                :show-loading=\"false\"\n              />\n              <van-field v-model=\"isPasswordData.userName\" type=\"tel\" placeholder=\"请输入用户名称\" />\n            </div>\n            <div class=\"form-content\">\n              <!-- 输入密码 -->\n              <van-image\n                width=\"40px\"\n                height=\"44px\"\n                fit=\"cover\"\n                :src=\"require('/src/assets/images/login/password.png')\"\n                :show-error=\"false\"\n                :show-loading=\"false\"\n              />\n              <van-field v-model=\"isPasswordData.password\" type=\"password\" placeholder=\"请输入用户密码\" />\n            </div>\n          </div>\n        </van-tab>\n      </van-tabs>\n      <div class=\"login-button-box\">\n        <Adaptive :data=\"['100%', '13.333%']\">\n          <van-button type=\"default\" class=\"login-button\" color=\"linear-gradient(78deg, #008EFF, #1DF2FF)\" @click=\"login\">\n            登录\n          </van-button>\n        </Adaptive>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport Regular from '@/utils/regular'\nimport User from '@/api/user'\nexport default {\n  components: {\n\n  },\n  data() {\n    return {\n      activeTab: 1,\n      isPasswordData: {\n        userName: '18928058888',\n        password: '123456'\n      },\n      isSMSData: {\n        phone: '',\n        code: ''\n      },\n      yzm_state: 0\n    }\n  },\n  methods: {\n    login() {\n      // activeTab:0 短信登录，activeTab:1 账号(手机号)登录\n      if (this.activeTab === 0) {\n        this.isSMSLogin()\n      } else {\n        this.isPasswordLogin()\n      }\n    },\n    // 获取手机号验证码\n    async getPhoneCode() {\n      var regular = new Regular()\n\n      const param = this.isSMSData.phone\n      if (!param) {\n        this.$Toast('手机号不能为空')\n        return\n      }\n      if (!regular.phone(param)) {\n        this.$Toast('手机号码输入有误')\n        return\n      }\n      // 距离上次点击未到60秒不让你进来\n      if (this.yzm_state) {\n        return\n      }\n      const res = await User.getPhoneCode(param)\n      if (res.code === 200) {\n        this.yzm_state = 60\n        const yzmTime = setInterval(() => {\n          if (this.yzm_state) {\n            this.yzm_state--\n          } else {\n            clearInterval(yzmTime)\n          }\n        }, 1000)\n      } else {\n        this.$Toast(res.message)\n        this.yzm_state = 0\n      }\n    },\n    /**\n     * 短信登录\n     */\n    async isSMSLogin() {\n      var regular = new Regular()\n      const param = {\n        phone: this.isSMSData.phone,\n        code: this.isSMSData.code\n      }\n      if (!param.phone) {\n        this.$Toast('账号不能为空')\n        return\n      }\n      if (!regular.phone(param.phone)) {\n        this.$Toast('手机号码输入有误')\n        return\n      }\n      if (!param.code) {\n        this.$Toast('验证码不能为空')\n        return\n      }\n      /**\n       * 暂时不请求接口，勿删此段注释代码\n       */\n      // const res = await this.$store.dispatch('LoginByCode', param)\n      // if (res.code === 200) {\n      // this.$Toast(res.message)\n      this.$Toast('登录成功')\n      this.$router.push('/home')\n      // } else {\n      //   this.$Toast(res.message)\n      // }\n    },\n    /**\n     *密码登录\n     */\n    async isPasswordLogin() {\n      var regular = new Regular()\n      const param = {\n        phone: this.isPasswordData.userName,\n        password: this.isPasswordData.password,\n        loginType: 0\n      }\n      if (!param.phone) {\n        this.$Toast('账号不能为空')\n        return\n      }\n      if (!regular.phone(param.phone)) {\n        this.$Toast('手机号码输入有误')\n        return\n      }\n      if (!param.password) {\n        this.$Toast('密码不能为空')\n        return\n      }\n      /**\n       * 暂时不请求接口，勿删此段注释代码\n       */\n      // const res = await this.$store.dispatch('Login', param)\n\n      // if (res.code === 200) {\n      //   this.$Toast(res.message)\n      this.$Toast('登录成功')\n      this.$router.push('/home')\n      // } else {\n      //   this.$Toast(res.message)\n      // }\n    }\n  }\n}\n</script>\n<style scoped src='./login.css'></style>\n<style>\n.login-box .van-tabs__nav--line{\n  border-top: 2px #263447 solid;\n  background-color: transparent\n}\n.login-box .van-tabs__line{\n  top: -2px;\n  bottom: unset;\n  background-color: #06F0FE;\n  width: 60px;\n}\n.login-box .van-tab{\n  font-size: 16px\n}\n.login-box .van-cell{\n  background-color: transparent;\n  padding-left: 7px;\n  font-size: 16px\n}\n.login-box .van-tabs--line .van-tabs__wrap{\n  width: 54%;\n  margin: 0 auto;\n}\n.login-box .van-tabs__content{\n  width: 80%;\n  margin: 0 auto\n}\n.login-box .form-box input::-webkit-input-placeholder {\n  color: #373F4A;\n}\n.login-box .form-box input::-moz-input-placeholder {\n  color: #373F4A;\n}\n.login-box .form-box input::-ms-input-placeholder {\n  color: #373F4A;\n}\n.login-box .form-box input:focus {\n  color: rgba(139, 163, 194, 1) !important\n}\n/* input失去焦点时文字颜色 */\n.login-box .van-field__control{\n  color: #8BA3C2\n}\n</style>\n"],"sourceRoot":""}]);
